"use client";

import { DashboardWrapper } from "@/components/layout/dashboard-wrapper";
import { Card, CardContent, CardFooter } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Plus, Calendar, Clock, Video, MapPin, User } from "lucide-react";
import { useState } from "react";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";
import { format } from 'date-fns';
import { SessionData } from "@/components/modals/session-detail-modal";

// Import the detail modal
import { SessionDetailModal } from "@/components/modals/session-detail-modal";
// Re-import ScheduleSessionModal
import { ScheduleSessionModal } from "@/components/scheduling/schedule-session-modal";

// Define the structure for session data
interface Session {
  id: string;
  title?: string;
  tutor: {
    name: string;
    avatar: string;
  };
  status: "upcoming" | "completed";
  type: "online" | "in-person";
  start: Date;
  end: Date;
}

// Function to get dates relative to today
const getDateRelativeToToday = (dayOffset: number, hour: number, minute: number = 0): Date => {
  const date = new Date();
  date.setDate(date.getDate() + dayOffset);
  date.setHours(hour, minute, 0, 0);
  return date;
};

// Mock data for demonstration - student's perspective with relative dates
const mockSessions: Session[] = [
  {
    id: "1",
    title: "Calculus Session w/ Dr. Carter",
    start: getDateRelativeToToday(1, 14), // Tomorrow 2 PM
    end: getDateRelativeToToday(1, 15),   // Tomorrow 3 PM
    tutor: { name: "Dr. Emily Carter", avatar: "/avatars/emily-carter.png" },
    status: "upcoming",
    type: "online",
  },
   {
    id: "2",
    title: "Physics Review w/ Dr. Grant",
    start: getDateRelativeToToday(2, 10), // Day after tomorrow 10 AM
    end: getDateRelativeToToday(2, 11, 30),// Day after tomorrow 11:30 AM
    tutor: { name: "Dr. Alan Grant", avatar: "/avatars/alan-grant.png" },
    status: "upcoming",
    type: "in-person",
  },
  {
    id: "3",
    title: "Past Calculus w/ Dr. Carter",
    start: getDateRelativeToToday(-5, 14), // 5 days ago 2 PM
    end: getDateRelativeToToday(-5, 15),   // 5 days ago 3 PM
    tutor: { name: "Dr. Emily Carter", avatar: "/avatars/emily-carter.png" },
    status: "completed",
    type: "online",
  },
];

export default function StudentSessionsPage() {
  // State for detail modal
  const [isDetailModalOpen, setIsDetailModalOpen] = useState(false);
  const [selectedSession, setSelectedSession] = useState<SessionData | null>(null);
  // Add state for schedule modal
  const [isScheduleModalOpen, setIsScheduleModalOpen] = useState(false);

  // Add mock available tutors for the scheduling modal
  const availableTutors = [
    { id: "tutor1", name: "Dr. Emily Carter", avatar: "/avatars/emily-carter.png" },
    { id: "tutor2", name: "Dr. Alan Grant", avatar: "/avatars/alan-grant.png" },
    // Add more tutors as needed
  ];

  // Function to open detail modal from list view
  const handleOpenDetailModal = (session: Session) => {
    // Transform Session to SessionData for the modal
    const sessionDataForModal: SessionData = {
      ...session,
      participant: {
        id: session.tutor.name, // Use tutor name as a temporary ID if no specific tutor ID exists
        name: session.tutor.name,
        avatar: session.tutor.avatar,
        role: "TUTOR"
      },
      // Ensure title is present or provide a default
      title: session.title || `Session with ${session.tutor.name}`
    };
    setSelectedSession(sessionDataForModal);
    setIsDetailModalOpen(true);
  };

  return (
    <DashboardWrapper>
      <div className="container mx-auto py-8">
        <div className="flex justify-between items-center mb-8">
          <h1 className="text-3xl font-bold">My Sessions</h1>
          <div className="flex gap-2">
            {/* Schedule Button (Opens Modal) */}
            <Button onClick={() => setIsScheduleModalOpen(true)}>
              <Plus className="mr-2 h-4 w-4" /> Schedule New Session
            </Button>
          </div>
        </div>

        {/* Always show List View */}
        <div className="space-y-6">
          {/* Upcoming Sessions */}
          <div>
            <h2 className="text-xl font-semibold mb-4">Upcoming Sessions</h2>
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
              {mockSessions.filter(s => s.status === "upcoming").length > 0 ? (
                mockSessions.filter(s => s.status === "upcoming").map(session => (
                  <Card key={session.id} className="hover:shadow-md transition-shadow cursor-pointer" onClick={() => handleOpenDetailModal(session)}>
                    <CardContent className="p-4 space-y-2">
                      {/* Tutor Info */}
                      <div className="flex items-center gap-3 mb-2">
                          <Avatar className="h-10 w-10 border">
                              <AvatarImage src={session.tutor.avatar} alt={session.tutor.name} />
                              <AvatarFallback>{session.tutor.name.split(' ').map(n => n[0]).join('')}</AvatarFallback>
                          </Avatar>
                          <div>
                              <p className="font-semibold text-sm">{session.tutor.name}</p>
                              <p className="text-xs text-muted-foreground">Tutor</p>
                          </div>
                      </div>
                      {/* Session Details */}
                      <div className="text-sm space-y-1 text-muted-foreground">
                          <div className="flex items-center gap-2">
                              <Calendar className="h-4 w-4"/> <span>{format(session.start, 'eee, MMM d, yyyy')}</span>
                          </div>
                          <div className="flex items-center gap-2">
                              <Clock className="h-4 w-4"/> <span>{format(session.start, 'p')} - {format(session.end, 'p')}</span>
                          </div>
                          <div className="flex items-center gap-2">
                            {session.type === 'online' ? <Video className="h-4 w-4"/> : <MapPin className="h-4 w-4"/>}
                            <span className="capitalize">{session.type}</span>
                          </div>
                      </div>
                    </CardContent>
                  </Card>
                ))
              ) : (
                <p className="text-muted-foreground col-span-full">No upcoming sessions scheduled.</p>
              )}
            </div>
          </div>

          {/* Past Sessions */}
          <div>
            <h2 className="text-xl font-semibold mb-4">Past Sessions</h2>
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
              {mockSessions.filter(s => s.status === "completed").length > 0 ? (
                mockSessions.filter(s => s.status === "completed").map(session => (
                  <Card key={session.id} className="hover:shadow-md transition-shadow cursor-pointer opacity-80" onClick={() => handleOpenDetailModal(session)}>
                    <CardContent className="p-4 space-y-2">
                      {/* Tutor Info */}
                      <div className="flex items-center gap-3 mb-2">
                          <Avatar className="h-10 w-10 border">
                              <AvatarImage src={session.tutor.avatar} alt={session.tutor.name} />
                              <AvatarFallback>{session.tutor.name.split(' ').map(n => n[0]).join('')}</AvatarFallback>
                          </Avatar>
                          <div>
                              <p className="font-semibold text-sm">{session.tutor.name}</p>
                              <p className="text-xs text-muted-foreground">Tutor</p>
                          </div>
                      </div>
                      {/* Session Details */}
                      <div className="text-sm space-y-1 text-muted-foreground">
                          <div className="flex items-center gap-2">
                               <Calendar className="h-4 w-4"/> <span>{format(session.start, 'eee, MMM d, yyyy')}</span>
                          </div>
                          <div className="flex items-center gap-2">
                               <Clock className="h-4 w-4"/> <span>{format(session.start, 'p')} - {format(session.end, 'p')}</span>
                          </div>
                          <div className="flex items-center gap-2">
                            {session.type === 'online' ? <Video className="h-4 w-4"/> : <MapPin className="h-4 w-4"/>}
                            <span className="capitalize">{session.type}</span>
                          </div>
                      </div>
                    </CardContent>
                  </Card>
                ))
              ) : (
                <p className="text-muted-foreground col-span-full">No past sessions recorded.</p>
              )}
            </div>
          </div>
        </div>
      </div>

      {/* Schedule Modal */}
      <ScheduleSessionModal
        isOpen={isScheduleModalOpen}
        onClose={() => setIsScheduleModalOpen(false)}
        role="STUDENT"
        availableUsers={availableTutors}
      />

      {/* Detail Modal */}
      <SessionDetailModal
        isOpen={isDetailModalOpen}
        onClose={() => setIsDetailModalOpen(false)}
        session={selectedSession}
        role="STUDENT"
      />
    </DashboardWrapper>
  );
} 