"use client";

import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogDescription,
  DialogFooter,
} from "@/components/ui/dialog";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { format } from "date-fns";
import { Calendar, Clock, User, Users, MapPin, Monitor } from "lucide-react";

// Update this interface to match the data structure used in session pages
export interface SessionData {
  id: string;
  title?: string; // Make title optional to match Session interface
  start: Date;
  end: Date;
  status: "upcoming" | "completed";
  type: "online" | "in-person";
  // Student or Tutor, depending on the context (who is viewing)
  participant: {
    id: string;
    name: string;
    avatar: string;
    role: "STUDENT" | "TUTOR"; // Indicate the role of the participant
  };
  // Add other details as needed, e.g., subject, notes
  subject?: string;
  notes?: string;
}

interface SessionDetailModalProps {
  isOpen: boolean;
  onClose: () => void;
  session: SessionData | null; // Use the updated SessionData type
  role: "STUDENT" | "TUTOR"; // Indicate the current user's role
}

export function SessionDetailModal({
  isOpen,
  onClose,
  session,
  role,
}: SessionDetailModalProps) {
  if (!session) return null;

  const participantLabel = session.participant.role === "STUDENT" ? "Student" : "Tutor";
  const isUpcoming = session.status === 'upcoming';
  // Generate a default title if none is provided
  const displayTitle = session.title || `Session with ${session.participant.name}`;

  return (
    <Dialog open={isOpen} onOpenChange={onClose}>
      <DialogContent className="sm:max-w-[480px]">
        <DialogHeader>
          <DialogTitle className="text-xl">{displayTitle}</DialogTitle>
          <DialogDescription>
            Details for your session.
          </DialogDescription>
        </DialogHeader>
        <div className="space-y-4 py-4">
          {/* Participant Info */}
          <div className="flex items-center gap-3">
            <Avatar>
              <AvatarImage src={session.participant.avatar} />
              <AvatarFallback>{session.participant.name.split(' ').map(n => n[0]).join('')}</AvatarFallback>
            </Avatar>
            <div>
              <p className="font-semibold text-sm">{session.participant.name}</p>
              <p className="text-xs text-muted-foreground">{participantLabel}</p>
            </div>
          </div>

          {/* Session Details Grid */}
          <div className="grid grid-cols-[auto,1fr] items-center gap-x-3 gap-y-2 text-sm">
            {/* Date */}
            <Calendar className="h-4 w-4 text-muted-foreground" />
            <span className="text-muted-foreground">{format(session.start, 'EEEE, MMMM d, yyyy')}</span>

            {/* Time */}
            <Clock className="h-4 w-4 text-muted-foreground" />
            <span className="text-muted-foreground">{`${format(session.start, 'p')} - ${format(session.end, 'p')}`}</span>

            {/* Type */}
            {session.type === 'online' ? <Video className="h-4 w-4 text-muted-foreground" /> : <MapPin className="h-4 w-4 text-muted-foreground" />}
            <span className="text-muted-foreground">{session.status === 'upcoming' ? 'Upcoming' : 'Completed'}</span>
          </div>

          {/* Subject and Notes */}
          {session.subject && (
            <div className="pt-2 border-t border-border">
              <h4 className="text-sm font-semibold mb-1">Subject</h4>
              <p className="text-sm text-muted-foreground">{session.subject}</p>
            </div>
          )}
          {session.notes && (
            <div className="pt-2 border-t border-border">
              <h4 className="font-semibold mb-1 text-sm">Notes</h4>
              <p className="text-sm text-muted-foreground whitespace-pre-wrap">{session.notes}</p>
            </div>
          )}
        </div>
        <DialogFooter className="gap-2 sm:justify-between">
          {/* Status Badge (moved to footer for prominence) */}
          <Badge variant={isUpcoming ? "default" : "secondary"} className="capitalize mr-auto">
            {isUpcoming ? "Upcoming" : "Completed"}
          </Badge>
          <Button variant="outline" onClick={onClose}>
            Close
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
} 